.imgCardsContainer{
    overflow: hidden;
    position: relative;
    width: 20vw;
    max-height: 200px;
    border: 2px solid black;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
}

.imgContainer img{
    width: auto;  
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
    border-radius: 9px;
    transition: 1s;
}
.imgCardsContainer:hover img{
    transform: scale(1.1);
}
.imgDesc{
    height: 100%;
    overflow: hidden;
    padding: 25px;
    position: absolute;
    display: flex;
    flex-direction: column;
    font-weight: 400;
    color: white;
    gap: 25px;
    top: 100%;
    background: #0000009c;
    backdrop-filter: blur(3px);
    transition: ease 1s;
    border-radius: 5px;
}
.imgCardsContainer:hover .imgDesc{
    top: 0;
}