@import url(https://fonts.googleapis.com/css2?family=Bonheur+Royale&family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&family=Outfit:wght@100..900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto+Condensed:ital,wght@0,100..900;1,100..900&family=Roboto:ital,wght@0,100..900;1,100..900&family=Winky+Rough:ital,wght@0,300..900;1,300..900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
    /* background: black;
    color: white; */
    padding: 0;
    margin: 0;
    font-family: Outfit,sans-serif;
}

body {
    min-height: 150vh;
}

.navbar{
    
    position: -webkit-sticky;
    
    position: sticky;
    top: 0;
    background: #fff;
    z-index: 1000;
}


.bigImage {
    display: flex;
    width: 100%;
    justify-content: center;
    margin-top: 40px;
}

.bigImgContainer .bigImage img {
    height: 80vh;
    border: solid 10px black;
    /* box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px; */
}

.imageCaption{
    display: flex;
    justify-content: center;
    margin: 20px;
    font-size: 2rem;
}
.intro{
    display: flex;
    justify-content: center;
    margin: 20px;
    font-size: 1/3rem;
    margin-bottom: 80px;
}
.heading{
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
    margin-top: 80px;
}
h1{
    font-weight: 400;
    font-size: 2.5em;
}
.cardsWrapper, .highlightsWrapper{
    margin-top: 30px;
}
.cardsContainer{
    position: relative;
    margin: auto;
    width: 70%;
    display: flex;
    grid-gap: 65px;
    gap: 65px;
    justify-content: space-around;
}
.highlightCardContainer{
    position: relative;
    margin: auto;
    width: 70%;
    display: flex;
    grid-gap: 40px;
    gap: 40px;
    justify-content: space-around;
}
.nav{
    height: 85px;
    display: flex;
    /* justify-content: center; */
    align-items: center;
    padding: 0px 40px;
    overflow: hidden;
}
.nav ul {
    list-style-type: none;
    display: flex;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
    /* color: white; */

}
.nav ul li a{
    text-decoration: none;
    color: black;
}

ul li img{
    max-height: 120px;
    margin-right: 30px;
}
.imgCardsContainer{
    overflow: hidden;
    position: relative;
    width: 20vw;
    max-height: 200px;
    border: 2px solid black;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
}

.imgContainer img{
    width: auto;  
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
    border-radius: 9px;
    transition: 1s;
}
.imgCardsContainer:hover img{
    transform: scale(1.1);
}
.imgDesc{
    height: 100%;
    overflow: hidden;
    padding: 25px;
    position: absolute;
    display: flex;
    flex-direction: column;
    font-weight: 400;
    color: white;
    grid-gap: 25px;
    gap: 25px;
    top: 100%;
    background: #0000009c;
    -webkit-backdrop-filter: blur(3px);
            backdrop-filter: blur(3px);
    transition: ease 1s;
    border-radius: 5px;
}
.imgCardsContainer:hover .imgDesc{
    top: 0;
}
footer{
    background: #000;
    color: white;
    min-height: 25vh;
    margin-top: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.logo img{

}
.bigContainer{
    display: flex;
    flex-direction: column;
    grid-gap: 15px;
    gap: 15px;
}
.bigContainer a{
    color: white;
    text-decoration: none;
}
.itemsContainer{
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    justify-content: center;
    align-items: center;
    font-size: 1.5em;
}

.highlightContainer{
    background: aliceblue;
    padding: 10px;
    min-height: 200px;
    width: 20vw;
    border-radius: 5px;
    overflow: hidden;
    transition: 0.2s;
    box-shadow: rgba(234, 228, 228, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;}
.highlightContainer:hover{
    transform: scale(1.04);
}
.highlightImgContainer{
    width: 20vw;
    height: 200px;
    overflow: hidden;
    border-radius: 5px;
}
.highlightImgContainer img{
    object-fit: cover;
    width: 20vw;
}
.topic{
    margin-top: 30px;
    padding-left: 10px;
}
.topic h3{
    font-weight: 500;
}
.tags{
    margin: 5px 0px 20px 10px;
    background: #a79c9c0b;
    padding: 4px 10px;
    font-size: 0.8em;
    width: -webkit-max-content;
    width: max-content;
    border-radius: 50px;
    border: 2px solid lightgray;
}
.desc{
    padding: 0px 10px;
}
.desc p{
    font-weight: 300;
    line-height: 1.5em;
    word-spacing: 0.1rem;
}
