
@import url('https://fonts.googleapis.com/css2?family=Bonheur+Royale&family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&family=Outfit:wght@100..900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto+Condensed:ital,wght@0,100..900;1,100..900&family=Roboto:ital,wght@0,100..900;1,100..900&family=Winky+Rough:ital,wght@0,300..900;1,300..900&display=swap');
* {
    /* background: black;
    color: white; */
    padding: 0;
    margin: 0;
    font-family: Outfit,sans-serif;
}

body {
    min-height: 150vh;
}

.navbar{
    
    position: sticky;
    top: 0;
    background: #fff;
    z-index: 1000;
}


.bigImage {
    display: flex;
    width: 100%;
    justify-content: center;
    margin-top: 40px;
}

.bigImgContainer .bigImage img {
    height: 80vh;
    border: solid 10px black;
    /* box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px; */
}

.imageCaption{
    display: flex;
    justify-content: center;
    margin: 20px;
    font-size: 2rem;
}
.intro{
    display: flex;
    justify-content: center;
    margin: 20px;
    font-size: 1/3rem;
    margin-bottom: 80px;
}
.heading{
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
    margin-top: 80px;
}
h1{
    font-weight: 400;
    font-size: 2.5em;
}
.cardsWrapper, .highlightsWrapper{
    margin-top: 30px;
}
.cardsContainer{
    position: relative;
    margin: auto;
    width: 70%;
    display: flex;
    gap: 65px;
    justify-content: space-around;
}
.highlightCardContainer{
    position: relative;
    margin: auto;
    width: 70%;
    display: flex;
    gap: 40px;
    justify-content: space-around;
}