.highlightContainer{
    background: aliceblue;
    padding: 10px;
    min-height: 200px;
    width: 20vw;
    border-radius: 5px;
    overflow: hidden;
    transition: 0.2s;
    box-shadow: rgba(234, 228, 228, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;}
.highlightContainer:hover{
    transform: scale(1.04);
}
.highlightImgContainer{
    width: 20vw;
    height: 200px;
    overflow: hidden;
    border-radius: 5px;
}
.highlightImgContainer img{
    object-fit: cover;
    width: 20vw;
}
.topic{
    margin-top: 30px;
    padding-left: 10px;
}
.topic h3{
    font-weight: 500;
}
.tags{
    margin: 5px 0px 20px 10px;
    background: #a79c9c0b;
    padding: 4px 10px;
    font-size: 0.8em;
    width: max-content;
    border-radius: 50px;
    border: 2px solid lightgray;
}
.desc{
    padding: 0px 10px;
}
.desc p{
    font-weight: 300;
    line-height: 1.5em;
    word-spacing: 0.1rem;
}